import { Octokit } from '@octokit/rest';

const octokit = new Octokit({
  auth: process.env.REACT_APP_GITHUB_TOKEN
});

const OWNER = 'HUN-II';
const REPO = 'wsms1';
const DATA_PATH = 'data';

// data 디렉토리 초기화
const initializeDataDirectory = async () => {
  try {
    await octokit.repos.getContent({
      owner: OWNER,
      repo: REPO,
      path: DATA_PATH,
    });
  } catch (error: any) {
    if (error.status === 404) {
      // data 디렉토리가 없으면 생성
      await octokit.repos.createOrUpdateFileContents({
        owner: OWNER,
        repo: REPO,
        path: `${DATA_PATH}/.gitkeep`,
        message: 'Initialize data directory',
        content: Buffer.from('').toString('base64'),
        branch: 'main',
      });
    }
  }
};

// 앱 시작 시 data 디렉토리 초기화
initializeDataDirectory();

export const githubService = {
  // 데이터 저장
  async saveData(date: string, data: any) {
    try {
      const content = JSON.stringify(data, null, 2);
      const path = `${DATA_PATH}/${date}.json`;
      
      // 파일이 이미 존재하는지 확인
      try {
        await octokit.repos.getContent({
          owner: OWNER,
          repo: REPO,
          path,
        });
        
        // 파일이 존재하면 업데이트
        const response = await octokit.repos.createOrUpdateFileContents({
          owner: OWNER,
          repo: REPO,
          path,
          message: `Update data for ${date}`,
          content: Buffer.from(content).toString('base64'),
          branch: 'main',
        });
        
        return response.data;
      } catch (error: any) {
        if (error.status === 404) {
          // 파일이 없으면 새로 생성
          const response = await octokit.repos.createOrUpdateFileContents({
            owner: OWNER,
            repo: REPO,
            path,
            message: `Create data for ${date}`,
            content: Buffer.from(content).toString('base64'),
            branch: 'main',
          });
          
          return response.data;
        }
        throw error;
      }
    } catch (error) {
      console.error('Error saving data:', error);
      throw error;
    }
  },

  // 데이터 불러오기
  async loadData(date: string) {
    try {
      const path = `${DATA_PATH}/${date}.json`;
      const response = await octokit.repos.getContent({
        owner: OWNER,
        repo: REPO,
        path,
      });

      if ('content' in response.data) {
        const content = Buffer.from(response.data.content, 'base64').toString();
        return JSON.parse(content);
      }
      return null;
    } catch (error: any) {
      if (error.status === 404) {
        return null;
      }
      console.error('Error loading data:', error);
      throw error;
    }
  },

  // 모든 날짜 데이터 불러오기
  async loadAllDates() {
    try {
      const response = await octokit.repos.getContent({
        owner: OWNER,
        repo: REPO,
        path: DATA_PATH,
      });

      if (Array.isArray(response.data)) {
        return response.data
          .filter(file => file.name.endsWith('.json'))
          .map(file => file.name.replace('.json', ''));
      }
      return [];
    } catch (error) {
      console.error('Error loading dates:', error);
      return [];
    }
  }
}; 